import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpModule } from '@angular/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ModalnewsPage } from '../pages/modalnews/modalnews';
import { MaintenancePage } from '../pages/maintenance/maintenance';
import { MtnhistoryPage } from '../pages/mtnhistory/mtnhistory';
import { MtnhistoryshowPage } from '../pages/mtnhistoryshow/mtnhistoryshow';
import { KeycardPage } from '../pages/keycard/keycard';
import { SelecttyperequestkeycardPage } from '../pages/selecttyperequestkeycard/selecttyperequestkeycard';
import { ReportproblemPage } from '../pages/reportproblem/reportproblem';
import { ProblemhistoryPage } from '../pages/problemhistory/problemhistory';
import { BluetoothPage } from '../pages/bluetooth/bluetooth';
import { ParcelinboxPage } from '../pages/parcelinbox/parcelinbox';
import { DecorationrequestPage } from '../pages/decorationrequest/decorationrequest';
import { DecorationhistoryPage } from '../pages/decorationhistory/decorationhistory';
import { StuffmovingrequestPage } from '../pages/stuffmovingrequest/stuffmovingrequest';
import { StuffmovinghistoryPage } from '../pages/stuffmovinghistory/stuffmovinghistory';
import { PhonebookPage } from '../pages/phonebook/phonebook';
import { LoginPage } from '../pages/login/login';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { UserProvider } from '../providers/user/user';
import { ConnectionProvider } from '../providers/connection/connection';
import { NewsProvider } from '../providers/news/news';
import { MaintenanceReqProvider } from '../providers/maintenance-req/maintenance-req';
import { ProblemRepProvider } from '../providers/problem-rep/problem-rep';
import { ParcelProvider } from '../providers/parcel/parcel';
import { DecorationProvider } from '../providers/decoration/decoration';
import { MovingStuffProvider } from '../providers/moving-stuff/moving-stuff';
import { StaffProvider } from '../providers/staff/staff';
import { KeycardProvider } from '../providers/keycard/keycard';

import { CallNumber } from '@ionic-native/call-number';
import { OneSignal } from '@ionic-native/onesignal';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ModalnewsPage,
    MaintenancePage,
    MtnhistoryPage,
    MtnhistoryshowPage,
    KeycardPage,
    SelecttyperequestkeycardPage,
    ReportproblemPage,
    ProblemhistoryPage,
    BluetoothPage, 
    ParcelinboxPage,
    DecorationrequestPage,
    DecorationhistoryPage,
    StuffmovingrequestPage,
    StuffmovinghistoryPage,
    PhonebookPage,
    LoginPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/modalnews/modalnews.module#ModalnewsPageModule', name: 'ModalnewsPage', segment: 'modalnews', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/maintenance/maintenance.module#MaintenancePageModule', name: 'MaintenancePage', segment: 'maintenance', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mtnhistoryshow/mtnhistoryshow.module#MtnhistoryshowPageModule', name: 'MtnhistoryshowPage', segment: 'mtnhistoryshow', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mtnhistory/mtnhistory.module#MtnhistoryPageModule', name: 'MtnhistoryPage', segment: 'mtnhistory', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/selecttyperequestkeycard/selecttyperequestkeycard.module#SelecttyperequestkeycardPageModule', name: 'SelecttyperequestkeycardPage', segment: 'selecttyperequestkeycard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/keycard/keycard.module#KeycardPageModule', name: 'KeycardPage', segment: 'keycard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reportproblem/reportproblem.module#ReportproblemPageModule', name: 'ReportproblemPage', segment: 'reportproblem', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/problemhistory/problemhistory.module#ProblemhistoryPageModule', name: 'ProblemhistoryPage', segment: 'problemhistory', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/bluetooth/bluetooth.module#BluetoothPageModule', name: 'BluetoothPage', segment: 'bluetooth', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ModalnewsPage,
    MaintenancePage,
    MtnhistoryPage,
    MtnhistoryshowPage,
    KeycardPage,
    SelecttyperequestkeycardPage,
    ReportproblemPage,
    ProblemhistoryPage,
    BluetoothPage,
    ParcelinboxPage,
    DecorationrequestPage,
    DecorationhistoryPage,
    StuffmovingrequestPage,
    StuffmovinghistoryPage,
    PhonebookPage,
    LoginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    UserProvider,
    ConnectionProvider,
    NewsProvider,
    MaintenanceReqProvider,
    ProblemRepProvider,
    ParcelProvider,
    DecorationProvider,
    MovingStuffProvider,
    StaffProvider,
    CallNumber,
    KeycardProvider,
    OneSignal
  ]
})
export class AppModule {}



